<template>
  <div id="main-container">
    <div class="input-wrapper">
      <input-text
        v-model="selected.figureName"
        name="figureName"
        :placeholder="'이름을 입력하세요'"
      />
      <button
        class="basic__button"
        @click="_pageInput"
      >
        검색
      </button>
    </div>
    <table class="responsive">
      <caption>
        {{ $route.meta.title }}
      </caption>
      <thead>
      <tr>
        <th scope="col">등급</th>
        <th scope="col">이름</th>
      </tr>
      </thead>
      <tbody>
      <tr
        v-for="(item, index) in paginationParams.content" :key="index"
        class="hover-tr"
        @click="_routeToGunplaDetail(item)">
        <td data-label="등급">{{ item.grade.code }}</td>
        <td data-label="이름">{{ item.figureName }}</td>
      </tr>
      </tbody>
    </table>
    <pagination
      v-model="paginationParams.currentPage"
      with-text
      :page-count="paginationParams.totalPages"
      :per-page="paginationParams.numberOfElements"
      @input="_pageInput"
    />
    <div class="input-wrapper" style="margin-top: 10px;">
      <button
        @click="_routeToGunplaWishCreate"
        class="basic__button">
        신규등록
      </button>
    </div>
  </div>
</template>

<script>
import { mapActions, mapState } from 'pinia'
import { figureStore } from '@/store/figures'
import { usersStore } from '@/store/users'
import { useCookies } from 'vue3-cookies'
import pagination from '@/components/global/pagination'
import sweetAlert from '@/wrapper/sweet-alert'
import jwtDecode from 'jwt-decode'

const { cookies } = useCookies()

export default {
  name: 'user-gunpla-wish-list',
  data () {
    return {
      selected: {
        figureName: ''
      },
      paginationParams: {
        currentPage: 1,
        content: [],
        totalPages: 0,
        number: 0,
        size: 10,
        numberOfElements: 0
      }
    }
  },
  components: { pagination },
  computed: {
    ...mapState(usersStore, ['userCustomInfo', 'loggedInAccessToken']),
    ...mapState(figureStore, ['getFigureWishList'])
  },
  methods: {
    ...mapActions(figureStore, ['setWishFigureList', 'setFigureWishInfo']),
    _routeToGunplaWishCreate () {
      const newFigureList = {
        selected: this.selected,
        paginationParams: this.paginationParams
      }
      this.setWishFigureList(newFigureList)
      this.$router.push({ name: 'user-gunpla-wish-create' })
    },
    _routeToGunplaDetail (item) {
      const newFigureList = {
        selected: this.selected,
        paginationParams: this.paginationParams
      }
      this.setFigureWishInfo(item)
      this.setWishFigureList(newFigureList)
      this.$router.push({ name: 'user-gunpla-wish-detail' })
    },
    _pageInput (page) {
      this._getPagedWishFigure(page - 1)
    },
    _getPagedWishFigure (page) {
      this.getPagedWishFigure(this.userCustomInfo.userOid, this.selected.figureName, page, this.paginationParams.size).then(res => {
        this.paginationParams = res
        this.paginationParams.currentPage = this.paginationParams.number + 1
      })
    }
  },
  created () {
    if (this.userCustomInfo === undefined) {
      sweetAlert.noIcon('잘못된 경로로 진입하셨거나 인증 정보가 없습니다. 로그인 화면으로 이동합니다.', '확인')
      this.logout()
    } else {
      if (new Date(jwtDecode(this.loggedInAccessToken).exp * 1000) <= new Date()) {
        sweetAlert.noIcon('자동 로그인 기간이 만료 되었습니다. 다시 로그인 해 주세요 :)', '확인')
        this.logout()
      }
    }
  },
  async mounted () {
    if (this.getFigureWishList.selected === undefined) {
      await this._getPagedWishFigure()
    } else {
      this.paginationParams.currentPage = this.getFigureWishList.paginationParams.currentPage
      this.selected = this.getFigureWishList.selected
      await this._pageInput(this.paginationParams.currentPage)
      cookies.remove('figureList')
    }
  }
}
</script>
<style scoped lang="scss">
.grid-item {
  height: 160px !important;
}
.button__wrapper {
  margin-top: 30px;
  text-align: right;
}
</style>
